export default function TablaEmpleados() {
  const [pageNumber, setPageNumber] = useState(1);
  const [dataEmpleados, setDataEmpleados] = useState([]);
  
  useEffect(() => {
    listarEmpleados(pageNumber, 20);
  },[]);

  async function listarEmpleados(page, limit) {
    await getEmpleados(true, 'listaTodo', page, limit).then((data) => {
      console.log(data);
    }).catch((error) => console.log(error));
  }

  const cambiarPagina = async (page, limit) => {
    
  };



  return (
    <Contenedor>
      <Table
        className="tabla-personal"
        title={HeaderEmpleados}
        columns={columns}
        dataSource={data}
        loading={false}
        size="small"
        scroll={{ x: '700px', y: '65vh' }}
        pagination={{
          current:pageNumber,
          total: data.length,
          defaultPageSize: 20,
          pageSizeOptions: [10,20,50,100],
          onChange: cambiarPagina
        }}
      />
    </Contenedor>
  )
}
