import React, { useState } from 'react';
import { Button, Input, Radio  } from 'antd';
import { PlusOutlined } from '@ant-design/icons';

const { Search } = Input;

export default function HeaderEmpleados() {
  const [tipoBusqueda, setTipoBusqueda] = useState("nombre");

  const buscarPersonal = (value) => {
    console.log(value);
  }

  return (
    <div className="header-tabla">
      <div className="busqueda">
        <Radio.Group 
          onChange={(e) => setTipoBusqueda(e.target.value)} 
          value={tipoBusqueda}
          style={{ paddingTop: '.22rem' }}
        >
          <Radio value="nombre">Nombre</Radio>
          <Radio value="numero_documento">Documento</Radio>
          <Radio value="carnet">Carnet</Radio>
        </Radio.Group>
        <Search
          placeholder="Buscar personal"
          allowClear
          enterButton={false}
          onSearch={buscarPersonal}
          style={{ width: 200, margin: 0 }}
        />
      </div>
      <div className="boton-agregar">
        <Button
          icon={<PlusOutlined/>}
        >
          Agregar
        </Button>
      </div>
    </div>
  )
}
