import { restPrivate } from './requestHelper';
/**
 * @param {boolean} toast false si debe buscar sin mostrar avisos
 * @param {number} page
 * @param {number} limit
 * @param {string} metodo
 */
export const getEmpleados = async (toast, metodo, page, limit) => restPrivate({
  url: 'empleados', method: 'GET', params: {metodo, page, limit}
}, toast);